.page-main {
  position: relative;
  text-decoration: none;
  background-color: @white;
}

.page-main__container {
  width: 320px;
  margin: 0 auto;
  padding-top: 95px;
  background-image: url("./../img/bg-back-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-position: top center;
}


.page-main__heading {
  margin: 0;
  padding: 31px 45px 86px;
  font-size: 24px;
  font-weight: 300;
  text-align: center;
  color: @white;
}

.page-main__heading--index {
  padding: 0 45px 75px;
}

@media (min-width: 660px) {
  .page-main__container {
    width: 660px;
    background-image: url("./../img/bg-back-tablet@1x.jpg");
    background-position: center top;
    background-repeat: no-repeat;
  }

  .page-main__heading {
    font-size: 50px;
    padding: 110px 91px 150px;

    &--index {
      font-size: 40px;
      padding: 103px 400px 0 78px;
    }
  }
}

@media (min-width: 1200px) {
  .page-main {
    position: relative;
  }

  .page-main__container {
    width: 1200px;
    position: relative;
    top: -90px;
    padding-top: 90px;
    background-image: url("./../img/bg-back-desktop@1x.jpg");
  }

  .page-main__heading--index {
    padding: 0 720px 0 59px;
    font-size: 50px;
  }
}
